*{
  margin:0px;
  padding:0px;
}
h1{
  font-family: 'Montserrat';
  font-size: 18px;
  color:black;
  margin-top: 20px;
  border-top: 2px solid #aaa7a7;
    border-bottom: 2px solid #aaa7a7;
    text-transform: capitalize;
    text-align: center;
    padding:5px;
}

.pokemonInfo{
  text-decoration: none;
  list-style: none;
    background-color: #fff;
    margin: 0;
    display: flex;
    justify-content: space-evenly;
    flex-direction: wrap;
    flex-wrap: wrap;
    padding: 30px 100px 100px;
}



.pokemonList{

  margin: 0 10px 20px 0;
  height: 230px;
  width: 170px;
  display: inline-block;
  border-radius: 20px;
  border: 1px solid #aaa7a7;
  background-color: rgba(228,238,241,.76);
  text-align: center;
  box-shadow: -1px 1px 7px 0px #888888;


}
.powers {
  list-style: none;
 display:flex;
 flex-direction: row;
 justify-content: center;
}
.powers ul{
  padding:0px;
  margin:auto;
  padding-inline-start:0px;
}
li{
  padding:20px;
  background-color: #aaa7a7;
  padding: 7px;
  border-radius: 22px;
  text-transform: uppercase;
  color: #fff;
  margin-right: 10px;
  margin-top:25px;
}